.headerLink {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  padding-left: 20px;
  padding-right: 20px;

  text-decoration: none;
  color: #e9e9e9;

  transition: all 0.2s ease-in-out;
}
.headerLink:hover {
  transition: all 0.2s ease-in-out;

  color: rgba(240, 255, 255, 0.5);
}
.headerLink:active {
  transition: all 0.2s ease-in-out;
  transform: scale(0.9);
}

