.homeSection {
  display: flex;
  flex-direction: column;

  width: 100%;
}

.titleSection {
  white-space: nowrap;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 8vw;

  animation: showGreeting 3s;
}

@keyframes showGreeting {
  from {opacity: 0;} to {opacity: 1;}
}

.image {
  margin-right: 15px;
  margin-left: 15px;

  object-fit: cover;

  width: 400px;
  height: 440px;
}
