.header {
  position: fixed;
  width: 100%;

  font-family: "Roboto", sans-serif;

  background-color: #000000;
  z-index: 1;
}
.headerShow {
  position: fixed;
  width: 100%;

  font-family: "Roboto", sans-serif;

  background-color: #000000;
  z-index: 1;

  animation: 1700ms show;

}
.items {
  display: flex;

  animation: 2000ms show;
}
@keyframes show {
  from {opacity: 0;} to {opacity: 1;}
}

.headerContainer{
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 4px 4rem;
}

.logo {
  margin-right: 15px;
  margin-left: 15px;

  object-fit: cover;

  width: 160px;
  height: 80px;
}


