.container{
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 20vw;
  height: 50%;
  max-height: 422px;

  border: 1px solid #141414;
  border-radius: 3px;

  padding: 10px;
  margin: 10px;

  text-decoration: none;
  color: #e9e9e9;
}

.miniCard{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  height: 100%;

  text-decoration: none;
  color: #e9e9e9;
}
.card {
  display: flex;
  flex-direction: column;
  align-items: center;

  height: 100%;

 /* width: 20vw;
  height: 50%;

  border: 1px solid #141414;
  border-radius: 3px;

  padding: 10px;
  margin: 10px;
  */

  cursor: pointer;
  text-decoration: none;
  color: #e9e9e9;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  width: 100%;
  padding: 5px;
  margin: 5px;
}
.title {
  font-size: 18px;
  margin-bottom: 4px;
  font-weight: bold;
}
.card:hover{
  transition: all 200ms ease-in-out;
  transform: scale(0.99);
}
.description {
  font-size: 14px;
  font-weight: 500;

  text-align: center;
  margin-top: 4px;
  margin-bottom: 4px;
}

@media (max-width: 1380px) {
  .container{
    width: 25%;
    height: 40%;
  }
}

@media (max-width: 900px) {
  .container{
    width: 40%;
    height: 40%;
  }
}

@media (max-width: 500px) {
  .container{
    width: 70%;
    height: 40%;
  }
}
