.menu {
  position: absolute;
  z-index: 5;

  background-color: rgba(20, 20, 20, 0.9);

  top: 92px;
  margin: 0 auto;
  padding: 10px;

  border: 1px solid #bebebe;
  max-height: 250px;
  width: 100%;

  animation: show 100ms ease-in-out;
}

@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.menuItem {
  display: flex;
  justify-content: center;
  margin: 2px;
}

.dropButton {
  padding: 10px;
  border: none;

  color: #fafafa;
  background: #000;
  font-size: 42px;
}
