.header {
  position: fixed;
  width: 100%;

  font-family: "Roboto", sans-serif;

  background-color: #000000;
  z-index: 1;
}
.headerShow {
  position: fixed;
  width: 100%;

  font-family: "Roboto", sans-serif;

  background-color: #000000;
  z-index: 1;
}
.items {
  display: flex;

  animation: 2000ms show;
}

.headerContainer{
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.logo {
  margin-right: 15px;
  margin-left: 15px;

  object-fit: cover;

  width: 160px;
  height: 80px;
}


