.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  width: 12%;
  height: 30%;

  border: 1px solid #141414;
  border-radius: 3px;

  padding: 10px;
  margin: 10px;

  text-decoration: none;
  color: #e9e9e9;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 100%;
  padding: 5px;
  margin: 5px;
}
.title {
  font-size: 18px;
  margin-bottom: 4px;
  font-weight: bold;
}
.grade {
  text-align: center;
  margin-top: 4px;
  margin-bottom: 4px;
}

@media (max-width: 1380px) {
  .card{
    width: 25%;
    height: 60vh;
  }
}
@media (max-width: 950px) {
  .card{
    width: 40%;
    height: 55vh;
  }
}
@media (max-width: 720px) {
  .card{
    width: 45%;
    height: 50vh;
  }
}
@media (max-width: 480px) {
  .card{
    width: 60%;
    height: 50vh;
  }
}
