.container{
  display: flex;
  justify-content: center;
  align-items: center;

  z-index: 4;

  height: 29%; /* 27% 100% */
  max-height: 422px;
  width: 20vw;
  background-color: #030303;
  position: absolute; /*none absolute*/

  border: 1px solid #141414;
  /*opacity: 0;*/

  animation: show 300ms ease-in-out;
}
@keyframes show {
  from{opacity: 0}
  to{opacity: 1}
}
/*.active{
  transition: all 300ms ease-in-out;
  opacity: 1;
}*/

.leftPart{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80%;
}

.rightPart{
  display: flex;
  justify-content: center;
  align-items: center;
}
.description{
  display: flex;
  font-size: 20px;
}
