.aboutSection {
  display: flex;
  flex-direction: column;

  position: relative;
  top: 93px;
}

.container {
  display: flex;
  flex-direction: column;

  max-width: 34%;
}

.sectionRow {
  display: flex;
  flex-direction: row;

  margin-bottom: 20px;
}

.sectionPart {
  display: flex;
  flex-direction: column;

  flex: 1;
}

.col{
  flex: 3;
}

.section {
  display: inline-flex;
  flex-direction: column;

  margin: 10px;
}

.sectionTitle {
  font-size: 24px;
  font-weight: 700;

  margin-bottom: 10px;
  border-bottom: 1px solid white;
}

.sectionParagraph {
  font-weight: 700;
}

.contacts {
  display: flex;
  justify-content: space-evenly;
  align-items: center;

  color: white;
  text-decoration: none;
}

.contacts:hover {
  transition: all 100ms ease-in-out;
  color: rgba(255, 255, 255, 0.7);
}

.contactTitle {
  width: 70%;
}

@media (max-width: 320px) {
  .container {
    width: 100%;
  }
}
