* {
  margin: 0;
  padding: 0;
}

html {
  font-family: "Roboto", sans-serif;
  background: rgb(3, 3, 3);

  color: white;
}
html::-webkit-scrollbar {
  width: 0;
}

.slide-in {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 105%;
  background: rgb(5, 5, 5);
  transform-origin: bottom;
  z-index: 2;
}

.slide-out {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 105%;
  background: rgb(5, 5, 5);
  transform-origin: top;
  z-index: 2;
}

