.progressBar {
  position: fixed;
  top: 92px;
  left: 0;
  right: 0;
  height: 5px;
  background: #ffffff;
  transform-origin: 0;
  z-index: 2;

  animation: showHeader 2s;
}

@keyframes showHeader {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media (max-width: 1280px) {
  .progressBar {
    top: 84px;
  }
}
